export type DialogType = {
    id: number;
    name: string;
}
export type MessageType = {
    id: number;
    message: string;
}
export type DialogsPageType = {
    dialogs: Array<DialogType>;
    messages: Array<MessageType>;
}

let initialState: DialogsPageType = {
    dialogs: [
        {id: 1, name: 'Vlad'},
        {id: 2, name: 'Sergey'},
        {id: 3, name: 'Natasha'},
        {id: 4, name: 'Alexander'},
        {id: 5, name: 'Philip'}
    ],
    messages: [
        {id: 1, message: 'Hello,my dear friend'},
        {id: 2, message: 'Ok'},
        {id: 3, message: 'Good news,how are you?'},
        {id: 4, message: '12:30,ok?'},

    ]
}


const dialogsReducer = (state = initialState, action: DialogsReducerActionsType): DialogsPageType => {
    switch (action.type) {
        case 'DIALOGS/ADD-MESSAGE': {
            let newMessage: MessageType = {
                id: 7,
                message: action.messageText
            }
            return {
                ...state,
                messages: [...state.messages, newMessage]
            }
        }
        default:
            return state
    }
}

export default dialogsReducer;


// ----------- actions -----------

export type DialogsReducerActionsType = AddMessageActionType

type AddMessageActionType = ReturnType<typeof addMessageAC>

export const addMessageAC = (messageText: string) => ({type: 'DIALOGS/ADD-MESSAGE', messageText} as const)